  src: url("../fonts/WixMadeforText-Medium.woff2") format("woff2"), url("../fonts/WixMadeforText-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "WixMadeforText";
  src: url("../fonts/WixMadeforText-SemiBold.woff2") format("woff2"), url("../fonts/WixMadeforText-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "WixMadeforText";
  src: url("../fonts/WixMadeforText-Bold.woff2") format("woff2"), url("../fonts/WixMadeforText-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "WixMadeforText";
  src: url("../fonts/WixMadeforText-ExtraBold.woff2") format("woff2"), url("../fonts/WixMadeforText-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}


html {
  font-size: 10px;
}

body {
  font-family: "WixMadeforText", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.6rem;
}

h1 {
  font-size: 68px;
  line-height: 1.2;
}




.header__wrapper {
  display: flex;
}
.header__logo-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(184, 22, 24);
  padding: 2.4rem 4.8rem;
}
.header__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.header__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__item:nth-child(1) {
  padding: 2rem 4.8rem;
  background-color: rgb(37, 43, 51);
  color: rgb(255, 255, 255);
}
.header__item:nth-child(2) {
  padding: 1.6rem 4.8rem;
}
.header__nav-list {
  display: flex;
  align-items: center;
  gap: 4.8rem;
}
.header__list-item.has-children {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}
.header__text-inner {
  display: flex;
  align-items: center;
  gap: 24px;
}
.header__text {
  max-width: 34.2rem;
  font-size: 1.6rem;
  color: rgb(188, 205, 224);
  text-align: right;
}
.header__info-wrapper {
  display: flex;
  gap: 4.8rem;
}
.header__info {
  font-weight: 600;
  font-size: 2.4rem;
}
.header__info-text {
  color: rgb(88, 104, 122);
  font-size: 1.4rem;
}
.header__button-wrapper {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}
.header__button-text {
  text-align: right;
  max-width: 29rem;
  font-size: 1.6rem;
  color: rgb(88, 104, 122);
}
.header__button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  padding: 1rem 4.8rem;
  background-color: rgb(184, 22, 24);
  color: rgb(255, 255, 255);
  font-weight: 600;
  gap: 12px;
}